import React from 'react';
import Repos from './components/Repos'
import './App.css';

class App extends React.Component {


  constructor(props) {
    super(props)
  
    this.state = {
       repos : [],
       title: ''
    }
  }

  onClick = () => {
    
    fetch(`https://api.github.com/users/${this.state.title}/repos#`)
    .then(res => res.json())
    .then(data => {
      var copy = [];
      var copyDesc = [];
      data.forEach(d => {
        copy.push(d.name)
        copyDesc.push(d.description)
      });

      this.setState({
          repos: copy
      }) 
    } )

    
  }

  onChange = (e) => {
    this.setState({ [e.target.name]: 
      e.target.value })
  }
  
  render(){
   
    return (
      <div >
        <h1 style={headerStyle}>GitHub Repo's Info</h1>
        <br />
        <form style={{display: 'flex', margin: '8px  auto ', width: '90%'}} >
          <input type='text' 
          name='title' 
          placeholder='enter username...' 
          onChange={this.onChange}
          style={{flex:10}}
         />

          <input type='button' 
          value='Search' 
          className='btn'
          onClick={this.onClick}
          style={{flex:1}}
           />

       
        </form>
        <br />
       
        <h2> Total Repositories - {this.state.repos.length}</h2>
        <br />
        {this.state.repos.map((repo, index) => <Repos 
        Repo={repo} 
        key={repo} 
        index={index}
         />)} 

        
        
     
      </div>
    );
  }
 
}

const headerStyle = {
  backgroundColor: '#2F4F4F',
  color: 'white',
  textAlign: 'center',
  padding: '7px'
}


export default App;
