import React, { Component } from 'react'

export class Repos extends Component {


    
    render() {
        return (
            <div style={repoStyle}>
                {this.props.index + 1}.{'   '}
                {this.props.Repo}

               
            </div>
        )
    }
}



const repoStyle = {
    backgroundColor: '#483D8B',
    color: 'white',
    width: '90%',
    margin: '0px auto',
    padding: '4px 10px',
    borderBottom: '2px solid white',
    borderRadius: '2px'
}


export default Repos
